.slantTitle {
    height: 5vh;
    padding-left: 20vw;
    font-size: 140%;
}

.slantInfoWrapper {
    padding: 0 10vw 0 26vw;
}

.slantUp {
    margin: 15vh 0;
    padding: 10vh 0;
    width: 100%;
    -webkit-animation: slantUp 2s ease-out .5s forwards;
            animation: slantUp 2s ease-out .5s forwards;
}

@-webkit-keyframes slantUp {
    to {
        -webkit-transform: skewY(-9deg);
                transform: skewY(-9deg);
    }
}

@keyframes slantUp {
    to {
        -webkit-transform: skewY(-9deg);
                transform: skewY(-9deg);
    }
}

.unSkew {
    -webkit-animation: unSkewUp 2s ease-out .5s forwards;
            animation: unSkewUp 2s ease-out .5s forwards;
}

@-webkit-keyframes unSkewUp {
    to {
        -webkit-transform: skewY(9deg);
                transform: skewY(9deg);
    }
}

@keyframes unSkewUp {
    to {
        -webkit-transform: skewY(9deg);
                transform: skewY(9deg);
    }
}

.slantDown {
    margin: 15vh 0;
    padding: 10vh 0;
    width: 100%;
    -webkit-animation: slantDown 2s ease-out .5s forwards;
            animation: slantDown 2s ease-out .5s forwards;
}

@-webkit-keyframes slantDown {
    to {
        -webkit-transform: skewY(9deg);
                transform: skewY(9deg);
    }
}

@keyframes slantDown {
    to {
        -webkit-transform: skewY(9deg);
                transform: skewY(9deg);
    }
}

.unSkewDown {
    -webkit-animation: unSkewDown 2s ease-out .5s forwards;
            animation: unSkewDown 2s ease-out .5s forwards;
}

@-webkit-keyframes unSkewDown {
    to {
        -webkit-transform: skewY(-9deg);
                transform: skewY(-9deg);
    }
}

@keyframes unSkewDown {
    to {
        -webkit-transform: skewY(-9deg);
                transform: skewY(-9deg);
    }
}

@media (min-width: 43em) {
    .slantUp {
        margin: 10vh 0;
        -webkit-animation: largeSlantUp 2s ease-out .5s forwards;
                animation: largeSlantUp 2s ease-out .5s forwards;
    }

    @-webkit-keyframes largeSlantUp {
        to {
            -webkit-transform: skewY(-3deg);
                    transform: skewY(-3deg);
        }
    }

    @keyframes largeSlantUp {
        to {
            -webkit-transform: skewY(-3deg);
                    transform: skewY(-3deg);
        }
    }

    .unSkew {
        -webkit-animation: largeUnSkewUp 2s ease-out .5s forwards;
                animation: largeUnSkewUp 2s ease-out .5s forwards;
    }

    @-webkit-keyframes largeUnSkewUp {
        to {
            -webkit-transform: skewY(3deg);
                    transform: skewY(3deg);
        }
    }

    @keyframes largeUnSkewUp {
        to {
            -webkit-transform: skewY(3deg);
                    transform: skewY(3deg);
        }
    }

    .slantDown {
        margin: 10vh 0;
        -webkit-animation: largeSlantDown 2s ease-out .5s forwards;
                animation: largeSlantDown 2s ease-out .5s forwards;
    }

    @-webkit-keyframes largeSlantDown {
        to {
            -webkit-transform: skewY(3deg);
                    transform: skewY(3deg);
        }
    }

    @keyframes largeSlantDown {
        to {
            -webkit-transform: skewY(3deg);
                    transform: skewY(3deg);
        }
    }

    .unSkewDown {
        -webkit-animation: largeUnSkewDown 2s ease-out .5s forwards;
                animation: largeUnSkewDown 2s ease-out .5s forwards;
    }

    @-webkit-keyframes largeUnSkewDown {
        to {
            -webkit-transform: skewY(-3deg);
                    transform: skewY(-3deg);
        }
    }

    @keyframes largeUnSkewDown {
        to {
            -webkit-transform: skewY(-3deg);
                    transform: skewY(-3deg);
        }
    }
}

@media (min-width: 50em) { 
    .slantTitle {
        height: 6vh;
        padding-left: 25.5vw;
        font-size: 160%;
    }
    
    .slantInfoWrapper {
        padding: 0 15vw 0 30vw;
    }

}