#homepage {
    height: 92vh;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: space-between;
}

.homeHalf {
    width: 100%;
    display: flex;
    flex-flow: column;
}

#homeLogo {
    height: 60%;
    align-items: center;
    justify-content: center;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% calc(100% - 5vh), 0 calc(100% - 5vh));
            clip-path: polygon(0 0, 100% 0, 100% calc(100% - 5vh), 0 calc(100% - 5vh));
    -webkit-animation: homeClipPath 4s ease-out .5s forwards;
            animation: homeClipPath 4s ease-out .5s forwards;
}

@-webkit-keyframes homeClipPath {
    from {
        -webkit-clip-path: polygon(0 0, 100% 0, 100% calc(100% - 5vh), 0 calc(100% - 5vh));
                clip-path: polygon(0 0, 100% 0, 100% calc(100% - 5vh), 0 calc(100% - 5vh));
    }
    to {
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 calc(100% - 10vh));
                clip-path: polygon(0 0, 100% 0, 100% 100%, 0 calc(100% - 10vh));
    }
}

@keyframes homeClipPath {
    from {
        -webkit-clip-path: polygon(0 0, 100% 0, 100% calc(100% - 5vh), 0 calc(100% - 5vh));
                clip-path: polygon(0 0, 100% 0, 100% calc(100% - 5vh), 0 calc(100% - 5vh));
    }
    to {
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 calc(100% - 10vh));
                clip-path: polygon(0 0, 100% 0, 100% 100%, 0 calc(100% - 10vh));
    }
}

#homeLogoIcon {
    width: 10em;
    opacity: 0;
    -webkit-animation: rotateLogo 4s ease-out .5s forwards;
            animation: rotateLogo 4s ease-out .5s forwards;
}

@-webkit-keyframes rotateLogo {
    0% {
        opacity: 0;
        -webkit-transform: scale(.25) rotate(0deg);
                transform: scale(.25) rotate(0deg);
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 1;
        -webkit-transform: scale(1) rotate(180deg);
                transform: scale(1) rotate(180deg);
    }
}

@keyframes rotateLogo {
    0% {
        opacity: 0;
        -webkit-transform: scale(.25) rotate(0deg);
                transform: scale(.25) rotate(0deg);
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 1;
        -webkit-transform: scale(1) rotate(180deg);
                transform: scale(1) rotate(180deg);
    }
}

#homeWords {
    height: 40%;
    padding: 0 10vw;
    align-items: center;
    justify-content: center;
    opacity: 0;
    -webkit-animation: wordAppearVert 4s .5s forwards;
            animation: wordAppearVert 4s .5s forwards;
}

@-webkit-keyframes wordAppearVert {
    from {
        opacity: 0;
        -webkit-transform: translateY(-20vh);
                transform: translateY(-20vh);
    }
    to {
        opacity: 1;
        -webkit-transform: translateY(0vh);
                transform: translateY(0vh);
    }
}

@keyframes wordAppearVert {
    from {
        opacity: 0;
        -webkit-transform: translateY(-20vh);
                transform: translateY(-20vh);
    }
    to {
        opacity: 1;
        -webkit-transform: translateY(0vh);
                transform: translateY(0vh);
    }
}

