.slantTop {
    padding: 5vh 15vw 15vh 19.5vw;
    font-size: 150%;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 85%, 0 85%);
            clip-path: polygon(0 0, 100% 0, 100% 85%, 0 85%);
    -webkit-animation: slantTitle 2s ease-out .5s forwards;
            animation: slantTitle 2s ease-out .5s forwards;
}

@-webkit-keyframes slantTitle {
    from {
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 85%, 0 85%);
                clip-path: polygon(0 0, 100% 0, 100% 85%, 0 85%);
    }
    to {
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 70%);
                clip-path: polygon(0 0, 100% 0, 100% 100%, 0 70%);
    }
}

@keyframes slantTitle {
    from {
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 85%, 0 85%);
                clip-path: polygon(0 0, 100% 0, 100% 85%, 0 85%);
    }
    to {
        -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 70%);
                clip-path: polygon(0 0, 100% 0, 100% 100%, 0 70%);
    }
}

#topSlant {
    margin-bottom: 3vh;
    height: 12vh;
    width: 100%;
    -webkit-transform: translateY(-6vh);
            transform: translateY(-6vh);
    -webkit-animation: slantDarkBlue 2s ease-out .5s forwards;
            animation: slantDarkBlue 2s ease-out .5s forwards;
}

@-webkit-keyframes slantDarkBlue {
    to {
        -webkit-transform: translateY(-6vh) skewY(3deg);
                transform: translateY(-6vh) skewY(3deg);
    }
}

@keyframes slantDarkBlue {
    to {
        -webkit-transform: translateY(-6vh) skewY(3deg);
                transform: translateY(-6vh) skewY(3deg);
    }
}

@media (min-width: 37.5em) { 
    .slantTop {
        font-size: 180%
    }
}

@media (min-width: 50em) {
    .slantTop {
        font-size: 220%;
        padding: 5vh 15vw 15vh 25vw;
    }
}